import {
  AppSidebar,
  AppFooter,
  AppHeader,
} from "../components/index";
import WidgetsDropdown from "../views/widgets/WidgetsDropdown";
import { getAllUserTypeFunc } from "../../services/Api";
import React, { useEffect, useState } from "react";

import {
  CAvatar,
  CButton,
  CButtonGroup,
  CCard,
  CCardBody,
  CCol,
  CRow,
  CTable,
  CTableBody,
  CTableDataCell,
  CTableHead,
  CTableHeaderCell,
  CTableRow,
} from "@coreui/react";
import { CChartLine } from "@coreui/react-chartjs";
import { getStyle, hexToRgba } from "@coreui/utils";
import CIcon from "@coreui/icons-react";
import { cilCloudDownload } from "@coreui/icons";

const DefaultLayout = () => {

  const [userData, setUserData] = useState([]);

  const [active, setActive] = useState();
  const random = (min, max) =>
    Math.floor(Math.random() * (max - min + 1) + min);
  const userGet = async () => {
    const user = await getAllUserTypeFunc();

    if (user.status === 200) {
      setUserData(user.data.data.user.reverse());
    } else {
      console.log("Error to get the user data");
    }
  };
  useEffect(() => {
    userGet();
  }, []);
  const convertDuration = (durationInMilliseconds) => {
    const seconds = Math.floor(durationInMilliseconds / 1000);
    const minutes = Math.floor(seconds / 60);
    const hours = Math.floor(minutes / 60);
    const weeks = Math.floor(hours / 168);
    const months = Math.floor(weeks / 4);
    const years = Math.floor(months / 12);

    return {
      seconds: seconds % 60,
      minutes: minutes % 60,
      hours: hours % 24,
      weeks,
      months,
      years,
    };
  };

  return (
    <div>
      <AppSidebar />
      <div className="wrapper d-flex flex-column min-vh-100 bg-light">
        <AppHeader />
        <div className="body flex-grow-1 px-3">
          <>
            <WidgetsDropdown />
            <CCard className="mb-4">
              <CCardBody>
                <CRow>
                  <CCol sm={5}>
                    <h4 id="traffic" className="card-title mb-0">
                      Traffic
                    </h4>
                    <div className="small text-medium-emphasis">
                      January - July 2021
                    </div>
                  </CCol>
                  <CCol sm={7} className="d-none d-md-block">
                    <CButton color="primary" className="float-end">
                      <CIcon icon={cilCloudDownload} />
                    </CButton>
                    <CButtonGroup className="float-end me-3">
                      {["Day", "Month", "Year"].map((value) => (
                        <CButton
                          color="outline-secondary"
                          key={value}
                          className="mx-0"
                          active={value === "Month"}
                        >
                          {value}
                        </CButton>
                      ))}
                    </CButtonGroup>
                  </CCol>
                </CRow>
                <CChartLine
                  style={{ height: "300px", marginTop: "40px" }}
                  data={{
                    labels: [
                      "January",
                      "February",
                      "March",
                      "April",
                      "May",
                      "June",
                      "July",
                    ],
                    datasets: [
                      {
                        label: "My First dataset",
                        backgroundColor: hexToRgba(getStyle("--cui-info"), 10),
                        borderColor: getStyle("--cui-info"),
                        pointHoverBackgroundColor: getStyle("--cui-info"),
                        borderWidth: 2,
                        data: [
                          random(50, 100),
                          random(50, 100),
                          random(50, 100),
                          random(50, 100),
                          random(50, 100),
                          random(50, 100),
                          random(50, 100),
                        ],
                        fill: true,
                      },
                      {
                        label: "My Second dataset",
                        backgroundColor: "transparent",
                        borderColor: getStyle("--cui-success"),
                        pointHoverBackgroundColor: getStyle("--cui-success"),
                        borderWidth: 2,
                        data: [
                          random(50, 200),
                          random(50, 200),
                          random(50, 200),
                          random(50, 200),
                          random(50, 200),
                          random(50, 200),
                          random(50, 200),
                        ],
                      },
                      {
                        label: "My Third dataset",
                        backgroundColor: "transparent",
                        borderColor: getStyle("--cui-danger"),
                        pointHoverBackgroundColor: getStyle("--cui-danger"),
                        borderWidth: 1,
                        borderDash: [8, 5],
                        data: [65, 65, 65, 65, 65, 65, 65],
                      },
                    ],
                  }}
                  options={{
                    maintainAspectRatio: false,
                    plugins: {
                      legend: {
                        display: false,
                      },
                    },
                    scales: {
                      x: {
                        grid: {
                          drawOnChartArea: false,
                        },
                      },
                      y: {
                        ticks: {
                          beginAtZero: true,
                          maxTicksLimit: 5,
                          stepSize: Math.ceil(250 / 5),
                          max: 250,
                        },
                      },
                    },
                    elements: {
                      line: {
                        tension: 0.4,
                      },
                      point: {
                        radius: 0,
                        hitRadius: 10,
                        hoverRadius: 4,
                        hoverBorderWidth: 3,
                      },
                    },
                  }}
                />
              </CCardBody>
            </CCard>

            <CRow>
              <CCol xs>
                <CCard className="mb-4">
                  <CCardBody>
                    <CTable align="middle" className="mb-0 border" hover responsive>
                      <CTableHead color="light">
                        <CTableRow>
                          <CTableHeaderCell className="text-center">
                            <CIcon />
                            Status
                          </CTableHeaderCell>
                          <CTableHeaderCell>User</CTableHeaderCell>
                          <CTableHeaderCell className="text-center">
                            Address
                          </CTableHeaderCell>
                          <CTableHeaderCell>Role</CTableHeaderCell>
                          <CTableHeaderCell className="text-center">
                            Phone Number
                          </CTableHeaderCell>
                          <CTableHeaderCell>Activity</CTableHeaderCell>
                        </CTableRow>
                      </CTableHead>
                      <CTableBody>
                        {Array.isArray(userData) ? (
                          userData.map((item, index) => (
                            <CTableRow key={index}>
                              <CTableDataCell className="text-center">
                                <CAvatar

                                  status={
                                    item.isActive
                                      ? "avatar-status bg-success"
                                      : "avatar-status bg-danger"
                                  }
                                >
                                  {item.isActive ? (
                                    <span className="sr-only">Active</span>
                                  ) : (
                                    <span className="sr-only">Inactive</span>
                                  )}
                                </CAvatar>
                              </CTableDataCell>
                              <CTableDataCell>
                                <div>{item.full_name}</div>
                                <div className="small text-medium-emphasis">
                                  <span>{item.new ? "New" : "Recurring"}</span> |
                                  Registered: {item.isActive}
                                </div>
                              </CTableDataCell>
                              <CTableDataCell className="text-center">
                                {/* <CIcon size="xl" icon={item.country} title={item.store_name} /> */}
                                {item.address}
                              </CTableDataCell>
                              <CTableDataCell>
                                <div className="clearfix">
                                  <div className="float-start">
                                    {/* <strong>{item.usage.value}%</strong> */}
                                    {item.role}
                                  </div>
                                  <div className="float-end">
                                    {/* <small className="text-medium-emphasis">{item.usage.period}</small> */}
                                  </div>
                                </div>
                                {/* <CProgress thin color={item.usage.color} value={item.usage.value} /> */}
                              </CTableDataCell>
                              <CTableDataCell className="text-center">
                                <> {item.phone_number.toString().padStart(11, "0")}</>
                              </CTableDataCell>
                              <CTableDataCell>
                                <div className="small text-medium-emphasis">
                                  Last login
                                </div>
                                <p>
                                  {convertDuration(
                                    new Date() - new Date(item.updated_at)
                                  ).years > 0 && (
                                      <>
                                        {
                                          convertDuration(
                                            new Date() - new Date(item.updated_at)
                                          ).years
                                        }{" "}
                                        years,{" "}
                                      </>
                                    )}
                                  {convertDuration(
                                    new Date() - new Date(item.updated_at)
                                  ).months > 0 && (
                                      <>
                                        {
                                          convertDuration(
                                            new Date() - new Date(item.updated_at)
                                          ).months
                                        }{" "}
                                        months,{" "}
                                      </>
                                    )}
                                  {convertDuration(
                                    new Date() - new Date(item.updated_at)
                                  ).weeks > 0 && (
                                      <>
                                        {
                                          convertDuration(
                                            new Date() - new Date(item.updated_at)
                                          ).weeks
                                        }{" "}
                                        weeks,{" "}
                                      </>
                                    )}
                                  {convertDuration(
                                    new Date() - new Date(item.updated_at)
                                  ).hours > 0 && (
                                      <>
                                        {
                                          convertDuration(
                                            new Date() - new Date(item.updated_at)
                                          ).hours
                                        }{" "}
                                        hours,{" "}
                                      </>
                                    )}
                                  {convertDuration(
                                    new Date() - new Date(item.updated_at)
                                  ).minutes > 0 && (
                                      <>
                                        {
                                          convertDuration(
                                            new Date() - new Date(item.updated_at)
                                          ).minutes
                                        }{" "}
                                        minutes,{" "}
                                      </>
                                    )}
                                  {convertDuration(
                                    new Date(item.updated_at) -
                                    new Date(item.created_at)
                                  ).seconds > 0 && (
                                      <>
                                        {
                                          convertDuration(
                                            new Date(item.updated_at) -
                                            new Date(item.created_at)
                                          ).seconds
                                        }{" "}
                                        seconds
                                      </>
                                    )}
                                </p>
                              </CTableDataCell>
                            </CTableRow>
                          ))
                        ) : (
                          <p>No user data available</p>
                        )}
                      </CTableBody>
                    </CTable>
                  </CCardBody>
                </CCard>
              </CCol>
            </CRow>
          </>
        </div>
        <AppFooter />
      </div>
    </div>
  );
};

export default DefaultLayout;
